@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&family=Nanum+Gothic&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.black {
  min-height: 47px;
  font-size: 27px;
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-main {
  /*background: linear-gradient(90deg, #00C9FF 0%, #92FE9D 100%);*/
  background-image: url("https://cuteyoru.cdn3.cafe24.com/main.gif");
  /* background-color: whitesmoke;*/
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  min-height: 97vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: calc(10px + 2vmin);
}

.App-docs {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: black;
}

.App-info {
  background-image: url("https://cuteyoru.cdn3.cafe24.com/evening.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-intro {
  background-image: url("https://cuteyoru.cdn3.cafe24.com/library.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 1700px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.footer-copyright {
  background-color: rgb(46, 46, 46);
  color: white;
}

.bold {
  font-weight: bold;
}

.bigtext {
  font-size: 57px;
}

.mdtext {
  font-size: 17px;
}

.mt10 {
  margin-top: 10px;
}

.margin {
   margin: 17px;
}

.nanum {
  font-family: 'Nanum Gothic', sans-serif;
}

.App-link {
  color: #61dafb;
}

.footer {
  background: rgb(29, 29, 29);
  color: white;
}

.arr {
  font-size: 50px;
  font-family: 'Do Hyeon', sans-serif;
}

.notfound {
  min-height: 100vh;
  color: whitesmoke;
  display: flex;
  flex-direction: column;
  align-items: center;
